// frontend/src/app/products/[id]/page.js
"use client";

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { useAuth } from '@/context/AuthContext';
import { useParams } from 'next/navigation';

export default function ProductDetailPage() {
  console.log("--- 1. Component is rendering or re-rendering ---");

  const params = useParams();
  const { id } = params;
  const { user } = useAuth();
  
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!id) {
      console.log("--- useEffect skipped: No ID found yet. ---");
      return;
    }

    const getProductDetails = async () => {
      console.log("--- 2. useEffect started: Preparing to fetch product with ID:", id, "---");
      setLoading(true);
      try {
        const res = await fetch(`http://localhost:5000/api/products/${id}`);
        console.log("--- 3. Fetch request sent. Response status:", res.status, "---");

        if (!res.ok) {
          throw new Error('Response not OK');
        }
        const data = await res.json();
        console.log("--- 4. Data received successfully from backend:", data, "---");
        
        setProduct(data);
        console.log("--- 5. Product state has been set. ---");

      } catch (error) {
        console.error("--- ERROR in fetch block:", error, "---");
        setProduct(null);
      } finally {
        setLoading(false); 
        console.log("--- 6. Finally block ran: setLoading has been set to false. ---");
      }
    };
    
    getProductDetails();
  }, [id]);

  console.log("--- 7. About to render. Current state is: loading =", loading, ", product =", !!product, "---");

  if (loading) {
    return <div className="text-center py-24 text-xl font-semibold">Loading product details...</div>;
  }

  if (!product) {
    return <div className="text-center py-24 text-xl font-semibold">Product not found.</div>;
  }

  return (
    <div className="text-center py-24">
        <h1 className="text-4xl font-bold">Product Loaded: {product.name}</h1>
        <p>If you see this, the component is working!</p>
    </div>
  );
}